<template>
  <v-app>
    <AppHeader @new-project="newProject" @open-project="openProject" @edit-project="editProject" @save-project="saveProject" />
    <v-row no-gutters>
      <v-col cols="12" sm="3">
        <AppSidebar
          @select-audio="selectAudio"
          @select-instrument="selectInstrument"
          @edit-audio="openEditAudio"
          @import-audio="openImportAudio"
          @import-instrument="openImportInstrument"
          @edit-instrument="openEditInstrument"
      />
      </v-col>
      <v-col cols="12" sm="9">
        <AppContent :selected-audio="projectStore.activeAudio" :selected-instrument="projectStore.activeInstrument" />
      </v-col>
    </v-row>



    <IntroModal :visible="showIntroModal && !audioCount" @openProject="introToOpen" @newProject="introToNew" />
    <UploadFirstAudio :visible="showUploadFirstAudio" @cancel="toIntro" @detectedDuration="detectedDuration = $event" @finish="showUploadFirstAudio = false; showEditProject = true"/>
    <EditProject :visible="showEditProject" :detectedDuration="detectedDuration" @close="showEditProject = false"/>

    <EditAudio :visible="showEditAudioModal" :audioIndex="selectedAudio" @close="closeEditAudio" />

    <ImportAudio :visible="showImportAudio" @close="closeImportAudio" />
    <ImportInstrument :visible="showImportInstrument" @close="closeImportInstrument" @loading="loading"/>

    <EditInstrument :visible="showEditInstrumentModal" :instrumentIndex="selectedInstrument" @close="closeEditInstrument" />
    <v-dialog persistent v-model="showLoading" max-width="800px">
      <v-card>
        <v-card-title>Processing...</v-card-title>
      </v-card>
    </v-dialog>
  </v-app>
</template>

<script>
import AppHeader from "@/components/AppHeader.vue";
import AppSidebar from "@/components/AppSidebar.vue";
import AppContent from "@/components/AppContent.vue";
import EditAudio from "@/components/Modals/EditAudio.vue";
import ImportAudio from "@/components/Modals/ImportAudio.vue";
import EditProject from "@/components/Modals/EditProject.vue";
import IntroModal from "@/components/Modals/IntroModal.vue";
import ImportInstrument from "@/components/Modals/ImportInstrument.vue";
import UploadFirstAudio from "@/components/Modals/UploadFirstAudio.vue";
import EditInstrument from "@/components/Modals/EditInstrument.vue";
import { editorService } from "@/EditorService";
import { projectStore } from "@/store/project";

export default {
  components: { AppHeader, AppSidebar, AppContent, EditAudio, EditInstrument ,IntroModal , UploadFirstAudio, EditProject, ImportAudio, ImportInstrument},
  data() {
    return {
      projectStore: projectStore,
      selectedAudio: null,
      selectedInstrument: null,
      showEditAudioModal: false,
      showEditProject: false,
      showEditInstrumentModal: false,
      showIntroModal: true,
      showUploadFirstAudio: false,
      showImportAudio: false,
      showImportInstrument: false,
      detectedDuration: '',
      showLoading: false
    };
  },
  computed: {
    audioCount: function(){
      window.debugg = projectStore;
      return projectStore.manifest.audios.length;}
  },
  methods: {
    introToOpen() {
      this.openProject();
    },
    introToNew() {
      this.showIntroModal = false;
      this.showUploadFirstAudio = true;
    },
    toIntro() {
      this.showIntroModal = true;
      this.showUploadFirstAudio = false;
    },
    newProject() {
      this.showIntroModal = true;
      editorService.clear();
    },
    openProject() {
      editorService.openFile((file)=>{
            editorService.loadProject(file);
          }
          ,
          "zip"
      );
    },
    editProject() {
      this.showEditProject = true;
    },
    async saveProject() {
      this.showLoading = true;
      await editorService.save();
      this.showLoading = false;
    },
    selectAudio(index) {
      this.selectedAudio = index;
    },
    selectInstrument(index) {
      this.selectedInstrument = index;
    },
    openEditAudio(index) {
      this.selectedAudio = index;
      this.showEditAudioModal = true;
    },
    closeEditAudio() {
      this.showEditAudioModal = false;
    },
    closeIntroModal() {
      this.showIntroModal = false;
    },
    openEditInstrument(index) {
      this.selectedInstrument = index;
      this.showEditInstrumentModal = true;
    },
    closeEditInstrument() {
      this.showEditInstrumentModal = false;
    },
    openImportAudio() {
      this.showImportAudio = true;
    },
    closeImportAudio() {
      this.showImportAudio = false;
    },
    openImportInstrument() {
      this.showImportInstrument = true;
    },
    closeImportInstrument() {
      this.showImportInstrument = false;
    },
    loading(value) {
      this.showLoading = value;
    }
  },
};
</script>
