<template>
  <v-dialog v-model="isVisible" max-width="800px">
    <v-card>
      <v-card-title>Edit Project</v-card-title>
      <v-card-text>
        <v-text-field label="Artist" v-model="artist" outlined></v-text-field>
        <v-text-field label="Title" v-model="title" outlined></v-text-field>
        <v-text-field label="Album" v-model="album" outlined></v-text-field>
        <v-text-field
            label="Year"
            type="number"
            v-model="year"
            outlined
        ></v-text-field>
        <v-text-field
            label="Length (seconds)"
            v-model="length"
            outlined
        ></v-text-field>
        <v-textarea
            label="Credits"
            v-model="credits"
            outlined
            rows="3"
        ></v-textarea>
        <v-file-input
            label="Cover Image"
            accept="image/*"
            v-model="coverImage"
            outlined
            @change="previewCover"
        ></v-file-input>
        <v-img
            v-if="coverPreview"
            :src="coverPreview"
            max-width="256"
            max-height="256"
            class="mt-3"
        ></v-img>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn @click="cancel">Cancel</v-btn>
        <v-btn color="primary" @click="saveProject">Save</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { projectStore } from "@/store/project";

export default {
  props: {
    visible: {
      type: Boolean,
      default: false,
    },
    detectedDuration: {
    },
  },
  data() {
    return {
      artist :"",
      title :"",
      album :"",
      year :"",
      length :"",
      credits :"",
      isVisible: this.visible, // Create local visibility state
      meta: { ...projectStore.manifest.meta },
      coverImage: null, // Original cover image file
      coverPreview: projectStore.coverImage
          ? URL.createObjectURL(projectStore.coverImage)
          : null,
    };
  },
  watch: {
    visible(val) {
      this.isVisible = val; // Sync prop with local state
    },
    isVisible(val) {
      if (!val) {
        this.$emit("close"); // Emit event when dialog is closed
        this.clear();
      }
    },
    detectedDuration(value) {
      this.length = value;
    }
  },
  methods: {
    clear() {
      this.artist = projectStore.manifest.meta.artist
      this.album = projectStore.manifest.meta.album
      this.title = projectStore.manifest.meta.artist
      this.year = projectStore.manifest.meta.year
      this.length = projectStore.manifest.meta.length
      this.credits = projectStore.manifest.meta.credits
    },
    cancel() {
      this.$emit("close");
      this.clear();
    },
    saveProject() {
      projectStore.manifest.meta.artist = this.artist;
      projectStore.manifest.meta.album = this.album;
      projectStore.manifest.meta.title = this.title;
      projectStore.manifest.meta.year = this.year;
      projectStore.manifest.meta.length = this.length;
      projectStore.manifest.meta.credits = this.credits;
      if (this.coverImage) {
        projectStore.coverImage = this.coverImage;
      }
      this.$emit("close");
      this.clear();
    },
    previewCover(event) {
      const file = event.target.files[0];
      if (file instanceof File || file instanceof Blob) {
        this.coverPreview = URL.createObjectURL(file);
      } else {
        console.warn("Invalid file for preview.");
        this.coverPreview = null; // Clear preview if invalid file
      }
    },
  },
};
</script>
