import {projectStore} from "@/store/project";

export const pianoService = {
    audioContext: new (window.AudioContext || window.webkitAudioContext)(),
    lastTime: -2,
    volume: 1,
    lastIndex: -1,
    notes: [],
    // Function to play a MIDI note
    play: function(midiNote, duration = 1) {
        const frequency = this.midiToFrequency(midiNote);
        const oscillator = this.audioContext.createOscillator();
        const gainNode = this.audioContext.createGain();

        // Set oscillator properties
        oscillator.type = 'sine'; // You can use 'sine', 'square', 'sawtooth', or 'triangle'
        oscillator.frequency.setValueAtTime(frequency, this.audioContext.currentTime);

        // Set up gain (volume envelope)
        gainNode.gain.setValueAtTime(this.volume * 0.5, this.audioContext.currentTime); // Start volume
        gainNode.gain.exponentialRampToValueAtTime(0.001, this.audioContext.currentTime + duration); // Fade out

        // Connect nodes
        oscillator.connect(gainNode);
        gainNode.connect(this.audioContext.destination);

        // Start and stop the oscillator
        oscillator.start(this.audioContext.currentTime);
        oscillator.stop(this.audioContext.currentTime + duration);
    },

    // Convert MIDI note to frequency
    midiToFrequency: function(midiNote) {
        return 440 * Math.pow(2, (midiNote - 69) / 12); // A4 = 440 Hz, MIDI note 69
    },

    updateNotes: function () {
        let out = [];
        if(projectStore.notes[projectStore.activeInstrument]) {
            projectStore.notes[projectStore.activeInstrument].forEach(function(noteGroup,index) {
                noteGroup.g.forEach(function(note) {
                    out.push(note)
                });
            });
        }
        this.notes = out;
    },

    updateAndSetStartTime:function (time) {
        console.log('updateAndSetStartTime');
        this.updateNotes();
        this.lastTime = time;
        let lastPastIndex = -1;
        for(let i = 0; i < this.notes.length; i++) {
            if(this.notes[i].time / 1000 < time) {
                lastPastIndex = i;
            }
        }
        this.lastIndex = lastPastIndex;
    },


    currentPlaying: function (time) {
        if(Math.abs(time - this.lastTime) > 1) {
            this.updateAndSetStartTime(time);
        }
        this.lastTime = time;
        if(this.notes[this.lastIndex +1] && ((this.notes[this.lastIndex +1].time / 1000) < time)) {
            this.play(this.notes[this.lastIndex +1].n, this.notes[this.lastIndex +1].length / 1000);
            this.lastIndex = this.lastIndex + 1;
            this.currentPlaying(time);
        }
    }
}
