<template>
  <v-dialog v-model="isVisible" max-width="600px">
    <v-card>
      <v-card-title>Edit Audio</v-card-title>
      <v-card-text>
        <v-text-field
            label="Description"
            v-model="description"
            outlined
        ></v-text-field>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn @click="cancel">Cancel</v-btn>
        <v-btn v-if="tracksCount > 1" color="red" @click="deleteAudio">Delete</v-btn>
        <v-btn color="primary" @click="saveAudio">Save</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { projectStore } from "@/store/project";

export default {
  props: {
    audioIndex: Number,
    visible: Boolean,
  },
  watch: {
    visible(val) {
      this.description = projectStore.manifest.audios[this.audioIndex]?projectStore.manifest.audios[this.audioIndex].description:'';
      this.isVisible = val; // Sync prop with local state
    },
    isVisible(val) {
      if (!val) {
        this.$emit("close"); // Emit event when dialog is closed
      }
    }
  },
  computed : {
    tracksCount() {
      return projectStore.manifest.audios.length;
    }
  },
  data() {
    return {
      description: "",
      isVisible: this.visible, // Create local visibility state
    };
  },
  methods: {
    cancel() {
      this.$emit("close");
    },
    deleteAudio() {
      projectStore.activeAudio = 0;
      let thisIndex = this.audioIndex;
      projectStore.manifest.audios.splice(this.audioIndex, 1);
      projectStore.audioFiles.splice(this.audioIndex, 1);
      // dropset instruments
      projectStore.manifest.instruments.forEach(function(value,index) {
        if(value.audio_id === thisIndex) {
          projectStore.manifest.instruments[index].audio_id = -1;
        }
        if(value.audio_id > thisIndex) {
          projectStore.manifest.instruments[index].audio_id --;
        }
      });

      this.$emit("close");
    },
    saveAudio() {
      projectStore.manifest.audios[this.audioIndex].description = this.description;
      this.$emit("close");
    },
  },
};
</script>
