<template>
  <v-dialog persistent v-model="isVisible" max-width="600px">
    <v-card>
      <v-card-title>Import Audio</v-card-title>
      <v-card-text>
        <v-btn size="large" color="primary" @click="uploadAudio">{{ uploadText }}</v-btn>
      </v-card-text>
      <v-card-text>
        <v-text-field
            label="First track description"
            v-model="description"
            outlined
        ></v-text-field>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn @click="cancel">Cancel</v-btn>
        <v-btn color="primary" @click="importAudio" :disabled="!audioFile || !description">
          Import
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { editorService } from "@/EditorService";
import { projectStore } from "@/store/project";

let uploadedFileData;
export default {
  props: {
    visible: {
      type: Boolean,
      default: true
    },
  },
  data() {
    return {
      isVisible: this.visible, // Create local visibility state
      audioFile: null,
      description: "",
    };
  },
  computed: {
    uploadText: function() {
      return this.audioFile?this.audioFile.name:'Upload first track';
    }
  },
  watch: {
    visible(val) {
      this.isVisible = val; // Sync prop with local state
    },
    isVisible(val) {
      if (!val) this.$emit("close"); // Emit event when dialog is closed
    },
  },
  methods: {
    /*uploadAudioFile(event) {
      const file = event.target.files[0];
      if (!file) return;
      const reader = new FileReader();
      reader.onload = (e) => {
        xmlContent.value = e.target.result;
        parseTracks(xmlContent.value);
      };
      reader.readAsText(file);
    },*/
    uploadAudio() {
      editorService.openFile(
          (file) => {
            this.audioFile = file;
          },
          (error) => {
            console.error(error);
          },
          "audio/*"
      );
    },
    cancel() {
      this.$emit("cancel");
    },
    importAudio() {
      const newAudio = {
        filename: this.audioFile.name,
        description: this.description || this.audioFile.name,
      };
      projectStore.manifest.audios.push(newAudio);
      editorService.addFileToStore(this.audioFile, "audio");
      editorService
          .calculateLength(this.audioFile)
          .then((duration) => {
            this.$emit("detectedDuration", duration);
          })
          .catch((error) => {
          });
      console.log(projectStore);
      projectStore.activeAudio = 0;
      this.$emit("finish");
    },
  },
};
</script>
