<template>
  <v-dialog v-model="isVisible" max-width="800px">
    <v-card>
      <v-card-title>Import Instrument</v-card-title>
      <v-card-text>
        <v-file-input
            v-if="step === 1"
            label="MusicXML File"
            accept=".xml"
            v-model="musicXmlFile"
            outlined
            required
            @change="parseTracks"
        ></v-file-input>
        <v-select
            v-if="step === 2"
            label="Instrument Track"
            :items="trackOptions"
            v-model="instrument.music_xml_track"
            item-title="title"
            item-value="value"
            outlined
            required
        ></v-select>
        <v-text-field
            v-if="step === 3"
            label="Description"
            v-model="instrument.description"
            outlined
        ></v-text-field>
        <v-select
            v-if="step === 3"
            label="Type"
            :items="instrumentTypes"
            v-model="instrument.type"
            item-title="text"
            item-value="value"
            outlined
        ></v-select>
        <v-select
            v-if="step === 3"
            label="Difficulty"
            :items="difficultyLevels"
            item-title="text"
            item-value="value"
            v-model="instrument.difficulty"
            outlined
        ></v-select>
        <v-select
            v-if="step === 3"
            label="Audio Track Reference"
            :items="audioOptions"
            v-model="instrument.audio_id"
            item-title="text"
            item-value="value"
            outlined
        ></v-select>


      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn @click="cancel">Cancel</v-btn>
        <v-btn v-if="step === 1" color="primary" @click="step = 2" :disabled="!musicXmlFile">
          Continue
        </v-btn>
        <v-btn v-if="step === 2" color="primary" @click="importInstrument" :disabled="!musicXmlFile">
          Import
        </v-btn>
        <v-btn v-if="step === 3" color="primary" @click="finishImport" :disabled="!instrument.description">
          Import
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { projectStore } from "@/store/project";
import { editorService } from "@/EditorService";

export default {
  props: {
    visible: Boolean,
  },
  data() {
    return {
      step: 1,
      isVisible: this.visible, // Create local visibility state
      musicXmlFile: null,
      originalXmlContent: null,
      filteredXmlContent: null,
      trackOptions: [],
      instrument: {
        type: 0,
        audio_id: -1,
        difficulty: 0,
        music_xml_file: "",
        music_xml_track: 0,
        json: "",
        description: "",
        sheetH: 0,
        sheetW: 0,
      },
      instrumentTypes: [
        { text: "Piano", value: 0 },
        { text: "Guitar", value: 1 },
        { text: "Bass", value: 2 },
        { text: "Vocals", value: 3 },
        { text: "Drums", value: 4 },
      ],
      difficultyLevels: [
        { text: "Easy", value: 0 },
        { text: "Medium", value: 1 },
        { text: "Hard", value: 2 },
      ],
    };
  },
  watch: {
    visible(val) {
      this.audioFile = null;
      this.description = "";
      this.isVisible = val; // Sync prop with local state
      this.step = 1;
      if(!val) {
        this.musicXmlFile = null;
        this.filteredXmlContent = null;
      }
    },
    isVisible(val) {
      if (!val) this.$emit("close"); // Emit event when dialog is closed
    },
  },
  computed: {
    audioOptions: function() {
      let out = [{value: -1, text: "No reference to audio"}];
      projectStore.manifest.audios.forEach(function(audio, index){
        out.push({
          text: audio.description || `Audio ${index + 1}`,
          value: index
        })});
      return out;
    },
  },
  methods: {
    cancel() {
      this.$emit("close");
      this.clear();
    },
    clear(){
      this.step = 1;
      this.musicXmlFile = null;
      this.originalXmlContent = null;
      this.filteredXmlContent = null;
      this.trackOptions = [];
      this.instrument.type = 0;
      this.instrument.audio_id = -1;
      this.instrument.difficulty = 0;
      this.instrument.music_xml_file = "";
      this.instrument.json = "";
      this.instrument.description = "";
      this.instrument.sheetH = 0;
      this.instrument.sheetW = 0;
    },
    parseTracks(event) {
      const file = event.target.files[0];
      if (file) {

        const reader = new FileReader();
        reader.onload = (e) => {
          const parser = new DOMParser();
          this.originalXmlContent = e.target.result;
          const doc = parser.parseFromString(e.target.result, "application/xml");
          const parts = doc.getElementsByTagName("score-part");
          this.trackOptions = Array.from(parts).map((part, index) => ({
            title: part.getElementsByTagName("part-name")[0].textContent || `Track ${index + 1}`,
            value: index//part.id,
          }));
          if(!this.trackOptions.length) {
            alert('Wrong MusicXML File');
          } else {
            this.instrument.music_xml_track = this.trackOptions[0].value;
          }
          //this.instrument.music_xml_file = file.name;
        };
        reader.readAsText(file);
      } else {
        this.originalXmlContent = null;
      }
    },
    importInstrument() {

      /// todo - remove this row. this is bug
      // this.filteredXmlContent = editorService.filterMusicXml(this.originalXmlContent, this.instrument.music_xml_track);
      this.step = 3;
      /*const newInstrument = { ...this.instrument };
      projectStore.manifest.instruments.push(newInstrument);
      projectStore.musicXmlFiles.push(this.musicXmlFile);
      this.$emit("close");*/
    },
    async finishImport() {
      this.$emit("close");
      this.$emit("loading",true);
      //projectStore.musicXmlFiles.push(this.filteredXmlContent);
      let answer = await editorService.getNotesJson(URL.createObjectURL(this.musicXmlFile), this.instrument.music_xml_track);
      projectStore.sheets.push(answer.img);
      projectStore.notes.push(answer.notes);
      this.instrument.sheetW = answer.width;
      this.instrument.sheetH = answer.height;
      projectStore.manifest.instruments.push(
          {
            type: this.instrument.type,
            audio_id: this.instrument.audio_id,
            difficulty: this.instrument.difficulty,
            music_xml_file: this.instrument.music_xml_file,
            music_xml_track: this.instrument.music_xml_track,
            json: this.instrument.json,
            description: this.instrument.description,
            sheetH: this.instrument.sheetH,
            sheetW: this.instrument.sheetW,
          }
          );
      projectStore.activeInstrument = 0;
      console.log(projectStore);
      this.$emit("loading",false);
      this.clear();
    },
  },
};
</script>
