import {reactive} from "vue";
import JSZip from "jszip";
import {projectStore} from "@/store/project";
// eslint-disable-next-line
import {OpenSheetMusicDisplay} from "@/libs/opensheetmusicdisplay/build/opensheetmusicdisplay.min";

export const osbService = {
    container: null,
    osmd: null,

    initContainer: function () {
        if (!this.container) {
            this.container = document.createElement("div");
            this.container.style.width = "1024px"; // Set a fixed width
            document.body.appendChild(this.container);
        }
    },

    initOsmd: async function () {
        this.initContainer();
        this.clear();
        if (!this.osmd) {
            this.osmd = await new OpenSheetMusicDisplay(this.container, {
                backend: "canvas", // Use 'canvas' backend
                autoResize: false,
            });
            this.osmd.zoom = 0.75;
        }
    },

    waitFullLoading: function () {
        return new Promise((resolve) => {
            const interval = setInterval(() => {
                if (this.osmd) {
                    this.osmd.render();
                    if (this.osmd && this.osmd.Cursor) {
                        clearInterval(interval);
                        setTimeout(() => {
                            this.osmd.render();
                            resolve(true);
                        }, 1000);
                    }
                }
            }, 100);
        });
    },

    loadXmlAsString: async function (xml, selectedTrack) {
        await this.initOsmd();
        await this.osmd.load(xml);
        this.osmd.sheet.Instruments.forEach((instrument, index) => {
            instrument.Visible = index === selectedTrack;
        });
        await this.waitFullLoading();
    },

    async calculateData(TrackID) {
        //this.clear();
        //await this.initOsmd();
        //await this.osmd.load(XML);
        //await this.waitFullLoading();
        /*this.osmd.sheet.Instruments.forEach((instrument, index) => {
            instrument.Visible = index === TrackID;
            console.log(index, index === TrackID);
        });*/

        let addedNotes = {}

        const element = document.getElementById("cursorImg-0");
        this.osmd.cursor.reset()
        this.osmd.cursor.show();

        const iterator = this.osmd.cursor.iterator;
        const allNotes = [];
        const beatRealValue = 0.25; // стандартное значение для доли
        let totalDuration = 0;
        const maxSteps = 10e6; // предотвращение бесконечных циклов
        let loopSteps = 0;

        while (!iterator.endReached) {
            const measure = iterator.CurrentMeasure;
            let tempoInBPM = measure.TempoInBPM;
            if (tempoInBPM === 0) {
                tempoInBPM = this.osmd.sheet.sheetStartBPM; // используем начальный BPM, если не указан
            }
            if(!tempoInBPM) {
                let testBpm = this.osmd.sheet?.playbackSettings?.beatsPerMinute;
                if(testBpm) {
                    tempoInBPM = testBpm;
                } else {
                    tempoInBPM = 120;
                }
            }

            const beatLengthInMs = 60000.0 / tempoInBPM; // длительность одной доли в миллисекундах

            // Добавляем длительность текущего такта
            const prevDuration = totalDuration;
            totalDuration += measure.Duration.RealValue * beatLengthInMs / beatRealValue;
            const measureStartByIterator = iterator.currentTimeStamp.realValue * 4 * 60000.0 / tempoInBPM;
            // Переход к следующему состоянию
            while (!iterator.endReached) {
                const voices = iterator.CurrentVoiceEntries;
                for (let i = 0; i < voices.length; i++) {
                    const voice = voices[i];
                    const notes = voice.notes;
                    let voiceGroup = [];
                    let InstrumentIndex = voice?.parentSourceStaffEntry?.parentStaff?.parentInstrument?.id;
                    for (let j = 0; j < notes.length; j++) {
                        const note = notes[j];
                        const noteDurationInMs = note.length.realValue * beatLengthInMs / beatRealValue;
                        if (note != null && note.halfTone !== 0 && !note.isRest()) {
                            const diff = (iterator.currentTimeStamp.realValue * 4 * 60000.0 / tempoInBPM) - measureStartByIterator;
                            const stringInfo = note.StringInstruction?.sourceNote?.stringNumberTab;
                            const fret = note.StringInstruction?.sourceNote?.fretNumber;
                            const time = prevDuration + diff;
                            const midiNote = note.halfTone + 12;
                            if (!addedNotes[midiNote + "_" + time]) {
                                voiceGroup.push({
                                    "n": midiNote,
                                    //"notw": note,
                                    time: time,
                                    length: noteDurationInMs,
                                    "s": stringInfo,
                                    "f": fret
                                });
                                addedNotes[midiNote + "_" + time] = true;
                            }

                        }
                    }
                    ///detect if
                    if(voiceGroup.length && InstrumentIndex === TrackID){
                        this.osmd.cursor.update();
                        this.osmd.cursor.show();
                        const left = parseFloat(element.style.left); // Gets the inline 'left' value
                        const top = parseFloat(element.style.top);
                        const height = element.clientHeight;
                        allNotes.push({
                            g:voiceGroup,
                            l: left,
                            t: top,
                            h: height
                        })
                    }

                }
                iterator.moveToNext();
                if (iterator.CurrentMeasure !== measure || iterator.backJumpOccurred) {
                    break; // переходим к следующему такту или повторению
                }
                loopSteps++;
                if (loopSteps >= maxSteps) {
                    console.error("Exceeded maximum loop limit while iterating");
                    break;
                }
            }
        }
        let canvas = this.container.querySelector("canvas");
        let img = await new Promise((resolve) => canvas.toBlob(resolve));
        let width = canvas.width;
        let height = canvas.height;
        this.clear();
        return {
            notes: allNotes,
            img: img,
            width: width,
            height: height
        };
    },
    clear: function () {
        this.initContainer();
        this.container.innerHTML = "";
        this.osmd = null;
    },
    next: async function () {

    }
}