<template>
  <v-list>
    <v-list-subheader>Audio tracks</v-list-subheader>
    <v-list-item
        v-for="audio in audios"
        :key="audio.id"
        :title="audio.title"
    >
      <template v-slot:append>
        <v-btn
            color="blue-darken-4"
            icon="mdi-pencil"
            variant="text"
            @click="editAudio(audio.id)"
        ></v-btn>
        <v-btn
            v-if="activeAudio !== audio.id"
            color="blue-darken-4"
            icon="mdi-radiobox-blank"
            @click="makeMainAudio(audio.id)"
            variant="text"
        ></v-btn>
        <v-btn
            v-if="activeAudio === audio.id"
            color="blue-darken-4"
            icon="mdi-radiobox-marked"
            variant="text"
        ></v-btn>
      </template>
    </v-list-item>
    <v-spacer class="mb-6">  </v-spacer>
    <v-btn v-if="canAdd" @click="addAudio" color="blue-darken-4" location="center">
      + Add audio track
    </v-btn>

    <v-list-subheader>Instrument tracks</v-list-subheader>
    <v-list-item
        v-for="instrument in instruments"
        :key="instrument.id"
        :title="instrument.title"
    >
      <template v-slot:append>
        <v-btn
            color="blue-darken-4"
            icon="mdi-pencil"
            variant="text"
            @click="editInstrument(instrument.id)"
        ></v-btn>
        <v-btn
            v-if="activeInstrument !== instrument.id"
            color="blue-darken-4"
            icon="mdi-radiobox-blank"
            @click="makeMainInstrument(instrument.id)"
            variant="text"
        ></v-btn>
        <v-btn
            v-if="activeInstrument === instrument.id"
            color="blue-darken-4"
            icon="mdi-radiobox-marked"
            variant="text"
        ></v-btn>
      </template>
    </v-list-item>
    <v-spacer class="mb-6"></v-spacer>
    <v-btn @click="addInstrument" color="blue-darken-4" location="center">
      + Add instrument track
    </v-btn>
  </v-list>
  <v-divider></v-divider>

</template>

<script>
 import { projectStore } from "@/store/project";

export default {
  props : {

  },
  data() {
    return {
      selectedAudio: null,
      projectStores: projectStore,
      selectedInstrument: null,
    };
  },
  computed : {
    canAdd: function() {
      return projectStore.manifest.audios.length < 8;
    },
    audios : function() {
      let out = [];
      projectStore.manifest.audios.forEach(function(value, index){
        out.push({
          id: index,
          title: value.description
        });
      });
      return out;
    },
    activeAudio: function () {
      return projectStore.activeAudio;
    },
    activeInstrument: function () {
      return projectStore.activeInstrument;
    },
    instruments : function() {
      let out = [];
      projectStore.manifest.instruments.forEach(function(value, index){
        out.push({
          id: index,
          title: value.description,
        });
      });
      return out;
    }
  },
  methods: {
    editAudio(index) {
      this.$emit("edit-audio", index);
    },
    makeMainAudio(index) {
      projectStore.activeAudio = index;
    },
    makeMainInstrument(index) {
      projectStore.activeInstrument = index;
    },
    editInstrument(index) {
      this.$emit("edit-instrument", index);
    },
    addAudio() {
      this.$emit("import-audio");
    },
    addInstrument() {
      this.$emit("import-instrument");
    }
  },
};
</script>
