<template>
  <div v-show="selectedAudio > -1">
    <div v-show="!showRefreshButton">
      <v-row no-gutters>
        <v-col cols="12" sm="10">
          <div id="waveform"></div>
          <v-row no-gutters class="mt-2">
            <v-col cols="12" sm="5">
              <v-checkbox v-model="simple" label="Hide some notes for better performance"></v-checkbox>
            </v-col>
            <v-col cols="12" sm="2">
              <v-btn icon="mdi-play" @click="play"></v-btn>
              <v-btn icon="mdi-stop" @click="stop"></v-btn>
            </v-col>
            <v-col cols="12" sm="2">
              Zoom:
              <v-slider v-model="zoom" min="1" max="1000"></v-slider>
            </v-col>
            <v-col cols="12" sm="2">
              <v-btn @click="placeSyncPoint">+ Sync</v-btn>
            </v-col>
            <v-col cols="12" sm="1">
              <v-btn icon="mdi-reload" @click="reRenderFull"></v-btn>
            </v-col>
          </v-row>
        </v-col>
        <v-col cols="12" sm="1">
          <v-slider direction="vertical" v-model="volAudio" min="0" max="1" label="Audio"></v-slider>
        </v-col>
        <v-col cols="12" sm="1">
          <v-slider direction="vertical" v-model="volInstrument" min="0" max="1" label="Instrument"></v-slider>
        </v-col>
      </v-row>

    </div>
    <div v-show="showRefreshButton" class="mt-10 text-center">
      <v-btn icon="mdi-reload" @click="reRenderFull"></v-btn>
    </div>


    <div ref="audioGraph" class="audio-graph"></div>
    <div ref="sheetMusic" class="sheet-music"></div>
  </div>
</template>

<script>
import WaveSurfer from "wavesurfer.js";
import RegionsPlugin from 'wavesurfer.js/dist/plugins/regions.esm.js'
import TimelinePlugin from 'wavesurfer.js/dist/plugins/timeline.esm.js'
import Hover from 'wavesurfer.js/dist/plugins/hover.esm.js'
import {onMounted} from "vue";
import {projectStore} from "@/store/project";
import {editorService} from "@/EditorService";
import {pianoService} from "@/PianoService";
import {osbService} from "@/OsmdService";
import {onBeforeUnmount} from "@vue/runtime-core";


export default {
  props: ["selectedAudio", "selectedInstrument"],
  data() {
    return {
      zoom: 10,
      volAudio: 1,
      volInstrument: 1,
      syncPoints: [],
      testv: 'varr',
      simple: true,
      wavesurfer: null,
      dynamicStyles: 'body{background-color: red}',
    };
  },
  computed: {
    showRefreshButton: function () {
      return !(projectStore.waveform);
    }
  },
  watch: {
    selectedAudio(val) {
      this.reRenderFull();
    },
    volAudio(value) {
      if(projectStore.waveform) {
        projectStore.waveform.setVolume(value)
      }
    },
    volInstrument(value) {
      pianoService.volume = value;
    },
    simple(val) {
      this.reRenderNotes();
    },
    selectedInstrument(val) {
      this.reRenderNotes();
    },
    dynamicStyles(newStyles) {
      let styleTag = document.getElementById("dynamic-waveform-styles");
      if (!styleTag) {
        styleTag = document.createElement("style");
        styleTag.id = "dynamic-waveform-styles";
        document.head.appendChild(styleTag);
      }
      if (styleTag) {
        styleTag.innerHTML = newStyles;
      }
    },
    zoom(val) {
      console.log(val);
      if (projectStore.waveform) {
        projectStore.waveform.zoom(val);
      }
    },
  },
  methods: {
    update: async function () {
      ///this.testv = await editorService.getNotesJson(projectStore.activeInstrument);
    },
    initWatchLoop: function () {
      this.removeWatchLoop();
      if (projectStore.waveform) {
        pianoService.updateAndSetStartTime(projectStore.waveform.getCurrentTime());
        projectStore.watchLoop = setInterval(() => {
          if (!projectStore.waveform) {
            this.removeWatchLoop();
          } else {
            if (projectStore.waveform.isPlaying()) {
              pianoService.currentPlaying(projectStore.waveform.getCurrentTime());
            }
          }

        }, 10);
      }
    },
    removeWatchLoop: function () {
      if (projectStore.watchLoop) {
        clearInterval(projectStore.watchLoop);
        projectStore.watchLoop = null;
      }
    },
    piano: function () {
      pianoService.play(77, 0.9);
    },
    placeSyncPoint: function () {
      if (projectStore.waveform) {
        let CurrentTime = projectStore.waveform.getCurrentTime();
        // do not allow add sync point on start
        if (!CurrentTime) {
          return;
        }
        // do not allow add sync point on exist position
        let allow = true;
        this.syncPoints.forEach((value) => {
          if (value.time === CurrentTime) {
            allow = false;
          }
        });
        if (!allow) {
          return;
        }
        let reg = projectStore.regions.addRegion({
          start: CurrentTime,
          content: 'Sync Point',
          color: 'rgb(0,255,21)',
          drag: true,
        });
        this.syncPoints.push({
          start: CurrentTime,
          reg: reg
        });
        console.log(this.syncPoints);
      }
    },
    next: function () {
      osbService.next();
    },
    play: function () {
      if (projectStore.waveform) {
        projectStore.waveform.play();
        this.initWatchLoop();
      }
    },
    stop: function () {
      if (projectStore.waveform) {
        this.removeWatchLoop();
        projectStore.waveform.pause();
      }
    },
    regionMoved: function (currentRegion) {
      if (!this.syncPoints.length) {
        return;
      }
      //console.log(currentRegion);
      //console.log(this.syncPoints);
      let wrongAction = false;
      this.syncPoints.forEach((reg, indexOfCurrentRegion) => {
        if (reg.reg.id !== currentRegion.id) {
          return;
        }
        let currentRegionId = currentRegion.id;
        let newStart = currentRegion.start;
        let oldStart = reg.start;
        // detect left and right borders
        let LeftBorder = 0;
        let RightBorder = 99999999999;
        this.syncPoints.forEach((borderRegion, indexOfCurrentBorderRegion) => {
          let currentBorderStart = borderRegion.start;
          if (indexOfCurrentBorderRegion === indexOfCurrentRegion) {
            return;
          }
          if (currentBorderStart < oldStart) {
            if (LeftBorder < currentBorderStart) {
              LeftBorder = currentBorderStart;
            }
          }
          if (currentBorderStart > oldStart) {
            if (RightBorder > currentBorderStart) {
              RightBorder = currentBorderStart;
            }
          }
        });
        //check maybe we overmoved it

        if (newStart < LeftBorder) {
          wrongAction = indexOfCurrentRegion;
          return;
        }
        if (newStart > RightBorder) {
          wrongAction = indexOfCurrentRegion;
          return;
        }
        /// process moving notes
        this.moveNotes(LeftBorder, RightBorder, oldStart, newStart);
        this.syncPoints[indexOfCurrentRegion].start = newStart;
        this.reRenderNotes();
        console.log(LeftBorder, newStart, RightBorder);

        //let OldValue =
        console.log('FOUND');
      });
      if (wrongAction !== false) {
        alert('Wrong action');
        //this.syncPoints.splice(wrongAction,1);
        this.reRenderNotes();
        return;
      }
      console.log(this.syncPoints);
    },


    ////
    rescalePointWithLength: function (value, length, oldMin, oldMax, newMin, newMax) {
      const scaleRatio = (newMax - newMin) / (oldMax - oldMin);
      const newValue = newMin + ((value - oldMin) * scaleRatio);
      const newLength = length * scaleRatio;
      return {newValue, newLength};
    },

    moveNotes: function (LeftBorder, RightBorder, oldStart, newStart) {
      if (projectStore.notes[projectStore.activeInstrument]) {
        projectStore.notes[projectStore.activeInstrument].forEach((groupValue, groupIndex) => {
          groupValue.g.forEach((noteValue, indexInGroup) => {
            let time = noteValue.time / 1000;
            let length = noteValue.length;
            if (time <= LeftBorder) {
              return;
            }
            if (time >= RightBorder) {
              return;
            }
            //calculate time first
            if (time < oldStart) {
              let data = this.rescalePointWithLength(time, length, LeftBorder, oldStart, LeftBorder, newStart);
              projectStore.notes[projectStore.activeInstrument][groupIndex].g[indexInGroup].time = data.newValue * 1000;
              projectStore.notes[projectStore.activeInstrument][groupIndex].g[indexInGroup].length = data.newLength;
            } else {
              let data = this.rescalePointWithLength(time, length, oldStart, RightBorder, newStart, RightBorder);
              projectStore.notes[projectStore.activeInstrument][groupIndex].g[indexInGroup].time = data.newValue * 1000;
              projectStore.notes[projectStore.activeInstrument][groupIndex].g[indexInGroup].length = data.newLength;
            }
          });
        });
      }
    },
    reRenderFull: function () {
      this.syncPoints = [];
      if (projectStore.waveform) {
        projectStore.waveform.destroy();
        projectStore.waveform = null
      }
      if (projectStore.activeAudio > -1 && projectStore.audioFiles[projectStore.activeAudio]) {
        const audioFile = projectStore.audioFiles[projectStore.activeAudio];
        if (audioFile instanceof Blob) {
          //const url = URL.createObjectURL(audioFile);
          //alert(url);
          projectStore.regions = RegionsPlugin.create();
          projectStore.regions.on('region-updated', (value) => {
            console.log(value);
            this.regionMoved(value)
          });
          projectStore.regions.on('region-clicked', (value, e)=> {
            let foundIndex = false;
            this.syncPoints.forEach((reg, indexOfCurrentRegion) => {
              if (reg.reg.id === value.id) {
                foundIndex = indexOfCurrentRegion;
              }
            });
            if(foundIndex !== false) {
              this.syncPoints.splice(foundIndex,1);
              this.reRenderNotes();
            }
          });

          projectStore.waveform = WaveSurfer.create({
            container: '#waveform',
            waveColor: '#4F4A85',
            height: 300,
            progressColor: '#383351',
            //url: url,
            plugins: [projectStore.regions, TimelinePlugin.create(), Hover.create({
              lineColor: '#ff0000',
              lineWidth: 2,
              labelBackground: '#555',
              labelColor: '#fff',
              labelSize: '11px',
            }),],
            dragToSeek: true,
          });
          projectStore.waveform.setVolume(this.volAudio);
          projectStore.waveform.loadBlob(audioFile);
          projectStore.waveform.on('init', () => {
            setTimeout(() => {
              this.reRenderNotes();
            }, 3000);
          });
        }

        //projectStore.waveform.load(audioFile);
        // this.reRenderNotes();
        //if(this.wavesurfer)
      }

    },
    reRenderNotes: function () {
      if (projectStore.waveform && projectStore.regions) {
        projectStore.regions.clearRegions();

        //adding sync points
        this.syncPoints.forEach((value, index) => {
          this.syncPoints[index].reg = projectStore.regions.addRegion({
            start: value.start,
            content: 'Sync Point',
            color: 'rgb(0,255,21)',
            drag: true,
          });
        });

        //adding notes
        if (projectStore.notes[projectStore.activeInstrument]) {
          let dynamicStyle = "";
          projectStore.notes[projectStore.activeInstrument].forEach((value, groupIndex) => {
            value.g.forEach((noteValue, indexInGroup) => {
              if(this.simple && indexInGroup) {
                return;
              }
              const regionId = `note-region-${groupIndex}-${indexInGroup}`;
              projectStore.regions.addRegion({
                id: regionId,
                start: noteValue.time / 1000,
                end: (noteValue.time + (noteValue.length * 0.99)) / 1000,
                content: '',
                color: 'rgba(255, 0, 0, 1)',
                drag: false,
                resize: false,
              });
              const {height, top} = this.calculateNoteStyle(noteValue.n);

              // Add styles to the dynamic string
              dynamicStyle += `
              div::part(${regionId}) {
                position: absolute;
                height: ${height};
                top: ${top};
              }
            `;
            });


          });
          this.dynamicStyles = dynamicStyle;
        }

      }
    },
    calculateNoteStyle(note) {
      const pianoRange = {min: 21, max: 108}; // 88-key piano range
      const wavesurferHeight = 300; // WaveSurfer container height
      const totalNotes = pianoRange.max - pianoRange.min + 1; // Total notes in piano range
      const noteHeight = wavesurferHeight / totalNotes; // Height of each note within the range
      const topOffset = wavesurferHeight - (note - pianoRange.min + 1) * noteHeight; // Inverted top position
      return {
        height: `${noteHeight}px`,
        top: `${topOffset}px`,
      };
    },
  },
  setup() {
    onBeforeUnmount(() => {
      if (projectStore.waveform) {
        projectStore.waveform.destroy();
        projectStore.waveform = null
      }
    });
    onMounted(() => {
    });
  }
};
</script>

