<template>
  <v-dialog v-model="isVisible" max-width="800px">
    <v-card>
      <v-card-title>Edit Instrument</v-card-title>
      <v-card-text>
        <v-text-field
            label="Description"
            v-model="instrument.description"
            outlined
        ></v-text-field>
<!--        <v-select
            label="Type"
            :items="instrumentTypes"
            v-model="instrument.type"
            item-title="text"
            item-value="value"
            outlined
        ></v-select>-->
        <v-select
            label="Difficulty"
            :items="difficultyLevels"
            v-model="instrument.difficulty"
            item-title="text"
            item-value="value"
            outlined
        ></v-select>
        <v-select
            label="Audio Track Reference"
            :items="audioOptions"
            item-title="text"
            item-value="value"
            v-model="instrument.audio_id"
            outlined
        ></v-select>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn @click="cancel">Cancel</v-btn>
        <v-btn color="red" @click="deleteInstrument">Delete</v-btn>
        <v-btn color="primary" @click="saveInstrument">Save</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { projectStore } from "@/store/project";

export default {
  props: {
    instrumentIndex: Number,
    visible: Boolean,
  },
  data() {
    return {
      isVisible: this.visible, // Create local visibility state
      instrument: {
        audio_id: -1,
        difficulty: 0,
        type: 0,
        description: "",
      },
      instrumentTypes: [
        { text: "Piano", value: 0 },
        { text: "Guitar", value: 1 },
        { text: "Bass", value: 2 },
        { text: "Vocals", value: 3 },
        { text: "Drums", value: 4 },
      ],
      difficultyLevels: [
        { text: "Easy", value: 0 },
        { text: "Medium", value: 1 },
        { text: "Hard", value: 2 },
      ],
    };
  },
  computed: {
    audioOptions: function() {
      let out = [{value: -1, text: "No reference to audio"}];
      projectStore.manifest.audios.forEach(function(audio, index){
        out.push({
          text: audio.description || `Audio ${index + 1}`,
          value: index
        })});
      return out;
    },
  },
  watch: {
    visible(val) {
      this.isVisible = val; // Sync prop with local state
      if(val) {
        this.instrument.audio_id = projectStore.manifest.instruments[this.instrumentIndex].audio_id;
        this.instrument.difficulty = projectStore.manifest.instruments[this.instrumentIndex].difficulty;
        this.instrument.type = projectStore.manifest.instruments[this.instrumentIndex].type;
        this.instrument.description = projectStore.manifest.instruments[this.instrumentIndex].description;
      }
    },
    isVisible(val) {
      if (!val) this.$emit("close"); // Emit event when dialog is closed
    },
  },
  methods: {
    cancel() {
      this.$emit("close");
    },
    deleteInstrument() {
      projectStore.activeInstrument = -1;
      projectStore.manifest.instruments.splice(this.instrumentIndex, 1);
      projectStore.sheets.splice(this.instrumentIndex, 1);
      projectStore.notes.splice(this.instrumentIndex, 1);
      this.$emit("close");
    },
    saveInstrument() {
      projectStore.manifest.instruments[this.instrumentIndex].audio_id = this.instrument.audio_id;
      projectStore.manifest.instruments[this.instrumentIndex].difficulty = this.instrument.difficulty;
      //projectStore.manifest.instruments[this.instrumentIndex].type = this.instrument.type;
      projectStore.manifest.instruments[this.instrumentIndex].description = this.instrument.description;
      this.$emit("close");
    },
  },
};
</script>
