<template>
  <v-dialog persistent v-model="isVisible" max-width="600px">
    <v-card>
      <v-card-title>Song editor</v-card-title>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="primary" @click="openProject">Open Project</v-btn>
        <v-btn color="primary" @click="newProject">New Project</v-btn>
      </v-card-actions>
    </v-card>

  </v-dialog>
</template>

<script>
export default {
  props: {
    audioIndex: Number,
    visible: {
      type: Boolean,
      default: true
    },
  },
  data() {
    return {
      isVisible: this.visible, // Create local visibility state
    };
  },
  watch: {
    visible(val) {
      this.isVisible = val; // Sync prop with local state
    },
    isVisible(val) {
      if (!val) this.$emit("close"); // Emit event when dialog is closed
    },
  },
  methods: {
    cancel() {
      //this.$emit("close");
    },
    openProject() {
      this.$emit("openProject");
    },
    newProject() {
      this.$emit("newProject");
    },
  },
};
</script>
